.formSearch {
  position: relative;
  top: 50%;
  left: 50%;
  width: 350px;
  height: 40px;
  margin-top: 30px;
  border-radius: 40px;
  box-shadow: 0 6px 8px rgba(0, 0, 0, 0.15);
  transform: translate(-50%, -50%);
  background: #fff;
  transition: all 0.3s ease;

  &.focus {
      box-shadow: 0 3px 4px rgba(0, 0, 0, 0.15);
    }
}


.searchInput {
  position: absolute;
  top: 10px;
  left: 38px;
  font-size: 14px;
  background: none;
  color: #5a6674;
  width: 195px;
  height: 20px;
  border: none;
  appearance: none;
  outline: none;
}

.searchButton {
  position: absolute;
  top: 10px;
  left: 15px;
  height: 20px;
  width: 20px;
  padding: 0;
  margin: 0;
  border: none;
  background: none;
  outline: none !important;
  cursor: pointer;
}

.icon {
  width: 20px;
  height: 20px;
}
